<template>
     <div :class="{'search-nav':true,'search-bg':searchBg,'isFixed':isFixed}" ref="search">
        <div class="back" @touchend="goBack">
            <i class="el-icon-arrow-left"></i>
        </div>
        <div class="search-con">
            <input type="text" placeholder="搜索店内商品" 
                    @focus="onFocus"
                    v-model="val"
                    @keyup="searchFood">
        </div>
        <div class="search-share" >
            <span v-if="searchList" @touchend="searchFood">搜索</span>
            <span v-else @touchend.stop="showShareList">•••</span>
            <ul class="share-list" v-if="isShareList">
                <li><i class="el-icon-connection"></i><span>分享商家</span></li>
                <li><i class="el-icon-chat-line-round"></i><span>联系商家</span></li>
                <li><i class="el-icon-warning-outline"></i><span>举报商家</span></li>
            </ul>
        </div>
    </div> 

</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default{
    props:['searchBg','isFixed','searchList','share'],
    data(){
        return{
            search:0,
            isShareList:false,
            val:''
        } 
    },
    mounted(){
        this.val = this.searchVal;
        this.search = this.$refs.search.offsetHeight;
        this.$emit('getSearchHeight',this.search);
    },
    methods:{
        goBack(){
            this.$router.back();
        },
        onFocus(){
            if(!this.searchList){
                console.log(this.$route.params)
                this.$router.push('/searchList');
            }
        },
        showShareList(){
            this.isShareList = !this.isShareList;
        },
        searchFood(){
            this.changeSearchVal(this.val);
        },
        ...mapMutations(['changeSearchVal'])
    },
    watch:{
        share(val){
            this.isShareList = false;
        },
        searchVal(val){
            this.val = val;
        }
    },
    computed:{
        ...mapState(['searchVal'])
    }
}
</script>

<style scoped lang="scss">
    @import "@/assets/css/public/search.scss"
</style>